import React, { Fragment, useRef } from "react";
import { useState } from "react";
import DataService from "../../services/data.service";

const AddressModal = () => {
  const form = useRef();
  const [name, setName] = useState("");
  const [pincode, setPincode] = useState("");
  const [house_no, setHouse_no] = useState("");
  const [area, setArea] = useState("");
  const [city, setCity] = useState("");
  const [state, setState] = useState("");
  const [location, setLocation] = useState("");
  const [loading, setLoading] = useState(false);

  const addAddress = (e) => {
    e.preventDefault();
    setLoading(name, pincode, house_no, area, city, state, location);
    DataService.addNewAddress(
      name,
      pincode,
      house_no,
      area,
      city,
      state,
      location
    ).then(
      (data) => {
        console.log(data);
        if (data.data.message.success_key === 1) {
          window.location.reload(false);
        }
        if (data.data.message.success_key === 0) {
          setLoading(false);
        }
      },
      (error) => {
        //console.log(error)
        if (error.response.message.success_key === 0) {
          setLoading(false);
        }
      }
    );
  };

  return (
    <Fragment>
      <div
        className="modal fade"
        id="addAddress"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content p-4">
            <div className="modal-header border-0">
              <h4 className="modal-title" id="exampleModalLabel">
                Add new address
              </h4>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <form onSubmit={addAddress} ref={form}>
                <div className="mb-3">
                  <input
                    type="text"
                    required
                    className="form-control"
                    placeholder="Your Name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="number"
                    required
                    className="form-control"
                    placeholder="Your Mobile Number"
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="number"
                    required
                    className="form-control"
                    placeholder="Pincode"
                    value={pincode}
                    onChange={(e) => setPincode(e.target.value)}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    required
                    className="form-control"
                    placeholder="Address ( House/Flat/Street)"
                    value={house_no}
                    onChange={(e) => setHouse_no(e.target.value)}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    required
                    className="form-control"
                    placeholder="Locality/Town"
                    value={area}
                    onChange={(e) => setArea(e.target.value)}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    required
                    className="form-control"
                    placeholder="City/District"
                    value={city}
                    onChange={(e) => setCity(e.target.value)}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    required
                    className="form-control"
                    placeholder="State"
                    value={state}
                    onChange={(e) => setState(e.target.value)}
                  />
                </div>
                <ul className="address-place mb-3">
                  <li>
                    <input
                      type="radio"
                      className="btn-check"
                      name="options-outlined"
                      id="success-outlined"
                      autoComplete="off"
                      defaultChecked
                      value={location}
                      onChange={(e) => setLocation(e.target.value)}
                    />
                    <label
                      className="btn btn-outline-dark home"
                      htmlFor="success-outlined"
                    >
                      Home
                    </label>
                  </li>
                  <li>
                    <input
                      type="radio"
                      className="btn-check"
                      name="options-outlined"
                      id="danger-outlined"
                      autoComplete="off"
                    />
                    <label
                      className="btn btn-outline-dark work"
                      htmlFor="danger-outlined"
                    >
                      Work
                    </label>
                  </li>
                  <li>
                    <input
                      type="radio"
                      className="btn-check"
                      name="options-outlined"
                      id="danger-outline"
                      autoComplete="off"
                    />
                    <label
                      className="btn btn-outline-dark other"
                      htmlFor="danger-outline"
                    >
                      Other
                    </label>
                  </li>
                </ul>
                <button
                  type="submit"
                  className="btn btn-primary w-100 bg-dark-black"
                  disabled={loading}
                >
                  {loading && (
                    <span className="spinner-border spinner-border-sm"></span>
                  )}
                  <span>SAVE ADDRESS</span>
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default AddressModal;
