import React from "react";
import { Link, useNavigate } from "react-router-dom";
import { useState } from "react";
import DataService from "../services/data.service";
import cookie from "js-cookie";
import Snackbar from "@mui/material/Snackbar";
import Alert from "@mui/material/Alert";
import RenderSmoothImage from "./RenderSmoothImage";
import "../core/styles.css";

const Product = (newArrival) => {
  const auth = cookie.get("user");

  const [inWishlistFlag, setInWishlistFlag] = useState(0);

  const [successShow, setSuccessShow] = useState(false);
  const [rejectShow, setRejectShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [successCartShow, setSuccessCartShow] = useState(false);
  const [rejectCartShow, setRejectCartShow] = useState(false);

  const handleClose = () => {
    setSuccessShow(false);
    setRejectShow(false);
    setSuccessCartShow(false);
    setRejectCartShow(false);
  };

  let sizeId = window.location.href.split("#")[1];
  let discounted_price;
  let original_price;
  let discount;

  const navigate = useNavigate();

  const add_to_wishlist = () => {
    if (auth) {
      setLoading(true);
      DataService.addToWishlist(
        newArrival.item_code,
        sizeId,
        discounted_price,
        original_price,
        discount
      ).then((data) => {
        if (data.data.message.Wishlist) {
          if (data.data.message.Wishlist.success_key === 1) {
            setSuccessShow(true);
            setLoading(false);
            setInWishlistFlag(1);
          }
        }
        if (data.data.message.success_key === 1) {
          setSuccessShow(true);
          setLoading(false);
          setInWishlistFlag(1);
        }
        if (data.data.message.success_key === 0) {
          setRejectShow(true);
          setLoading(false);
          setInWishlistFlag(1);
        }
      });
    } else {
      navigate("/login");
    }
  };

  return (
    <>
      <Snackbar
        open={successShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="success"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "4px",
              opacity: "1",
            },
          }}
          style={{
            background: "green",
            color: "white",
          }}
        >
          <b>Successfully Added to Wishlist</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={rejectShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="info"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "3px",
              opacity: "1",
            },
          }}
          style={{
            background: "black",
            color: "white",
            fontWeight: "500",
          }}
        >
          <b>Already present in Wishlist</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={successCartShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="success"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "4px",
              opacity: "1",
            },
          }}
          style={{
            background: "green",
            color: "white",
          }}
        >
          <b>Successfully Added to Bag</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={rejectCartShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="info"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "3px",
              opacity: "1",
            },
          }}
          style={{
            background: "black",
            color: "white",
            fontWeight: "500",
          }}
        >
          <b>Already present in Bag</b>
        </Alert>
      </Snackbar>
      <section className="u-line section-padding">
        <div className="container">
          <div className="row">
            {newArrival?.newArrival?.length > 0
              ? newArrival.newArrival.map((item, i) => (
                  <div key={i} className="col-12 col-md-4 mt-3">
                    <div className=" p-5 prod-box">
                      {inWishlistFlag === 0 ? (
                        <button
                          className="btn fa fa-heart wishlist-heart"
                          onClick={add_to_wishlist}
                        >
                          {loading && (
                            <span className="spinner-border spinner-border-sm"></span>
                          )}{" "}
                          <span>ADD TO WISHLIST</span>
                        </button>
                      ) : (
                        <button
                          disabled
                          className="btn fa fa-heart wishlist-heart-active"
                        >
                          <span>WISHLISTED</span>
                        </button>
                      )}
                      <Link
                        to={
                          "/detail/" + item.id + "#" + item.title.split("-")[1]
                        }
                        className="text-decoration-none"
                      >
                        <RenderSmoothImage
                          src={item.image}
                          alt={item.title}
                          className="img-fluid"
                        />
                      </Link>
                      <h2 className="h5 text-center mt-3 mb-3">{item.title}</h2>
                      <p className="text-center">
                        {item.description} <br></br>
                        <span className="price">Rs. {item.price}</span>
                      </p>
                    </div>
                  </div>
                ))
              : ""}
          </div>
        </div>
      </section>
    </>
  );
};

export default Product;
