import React from 'react'
import { useEffect } from 'react'
import { useState } from 'react'
import { Fragment } from 'react'
import { Link } from 'react-router-dom'

export const SelectSizeModal = (props) => {
    let sizes = [];
    const [sizeIds, setSizeIds] = useState([]);

    useEffect(() => {
        getSizes();
        // eslint-disable-next-line
    }, [props.data]);

    const getSizes = () => {
        if (props.data.length > 0) {
            props.data.forEach(item => {
                sizes.push(item.id)
            })
            setSizeIds(sizes);
        }
    }

    const selectSize = (size)=>{
        props.size(size);
    }

    return (
        <Fragment>
            <div className="modal fade" id="selectSize" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog">
                    <div className="modal-content p-4">
                        <div className="modal-header border-0">
                            <h4 className="modal-title" id="exampleModalLabel">Select Size</h4>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        {
                            sizeIds.length > 0 ?
                                <ul className="productSize">
                                    {sizeIds.map((text, i) => (
                                        <li key={i}>
                                            <Link
                                                to={``}
                                                onClick={()=>selectSize(text)}
                                                data-bs-toggle="modal" data-bs-target="#selectSize"
                                                >
                                                <span key={i}>{text}</span>
                                            </Link>
                                        </li>
                                    ))}
                                </ul> : ""
                        }
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
