import React from "react";
import Footer from "../core/Footer";
import Navbar from "../core/Navbar";
import Content from "../sections/mybag/content";
import "../core/styles.css";

const MyBag = () => {
  React.useEffect(() => {
    document.title = "Exclusive Elements - My Bag";
  }, []);
  return (
    <div className="app">
      <Navbar />
      <Content />
      <Footer />
    </div>
  );
};

export default MyBag;
