import React from "react";
import Navbar from "../core/Navbar";
import "../core/styles.css";
import Content from "../sections/register/Content";

const Register = () => {
  React.useEffect(() => {
    document.title = "Exclusive Elements - Register";
  }, []);

  return (
    <div className="app">
      <Navbar />
      <Content />
    </div>
  );
};

export default Register;
