import axios from "axios";

const API_URL = process.env.REACT_APP_PROXY_URL;

const getProduct = () => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.product.get_products")
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const getProductDetail = (item_code) => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.product.get_product", {
      item_code,
    })
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error.response;
    });
};

const getCategory = (item_group, start, page_length, players) => {
  return axios
    .post(
      API_URL + `method/sole_erp_next.api.product.get_products_based_category`,
      {
        item_group,
        start,
        page_length,
        players,
      }
    )
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const getWishlist = () => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.check_products.check_products", {
      items: {
        order_type: "Wishlist",
      },
    })
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const getCart = () => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.check_products.check_products", {
      items: {
        order_type: "Shopping Cart",
      },
    })
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const addToWishlist = (
  item_code,
  id,
  discounted_price,
  original_price,
  discount
) => {
  return axios
    .post(
      API_URL + "method/sole_erp_next.api.wishlist.create_products_wishlist",
      {
        items: {
          item_code: item_code,
          id: id,
          discounted_price: discounted_price,
          original_price: original_price,
          discount: discount,
        },
      }
    )
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const addToCart = (
  item_code,
  id,
  discounted_price,
  original_price,
  discount
) => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.cart.create_products_cart", {
      items: {
        item_code: item_code,
        id: id,
        discounted_price: discounted_price,
        original_price: original_price,
        discount: discount,
      },
    })
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const moveToCart = (product, size) => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.cart.create_products_cart", {
      items: {
        item_code: product.item_code.split("-")[0],
        id: size,
        discounted_price: product.discounted_price,
        original_price: product.price,
        discount: product.discount_amount,
      },
    })
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const removeFromWishlist = (item_code) => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.delete_product.delete_products", {
      item: {
        order_type: "Wishlist",
        items: [
          {
            item_code: item_code,
          },
        ],
      },
    })
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const removeFromCart = (toBeRemoved) => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.delete_product.delete_products", {
      item: {
        order_type: "Shopping Cart",
        items: toBeRemoved,
      },
    })
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const addNewAddress = (
  name,
  pincode,
  house_no,
  area,
  city,
  state,
  location
) => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.address.create_address", {
      address_details: {
        name: name,
        pincode: pincode,
        house_no: house_no,
        area: area,
        city: city,
        state: state,
        location: location,
      },
    })
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const getAllAddress = () => {
  return axios
    .get(API_URL + "method/sole_erp_next.api.address.get_all_addresses")
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const createNewOrder = (items) => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.order.create_order", {
      items,
    })
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const getAssetUrl = () => {
  return axios
    .get(API_URL + "method/sole_erp_next.api.payment.get_gravity_assets_url")
    .then((result) => {
      return result;
    })
    .catch((error) => {
      return error;
    });
};

const getTransactionToken = (amount) => {
  return axios
    .post(API_URL + "method/sole_erp_next.api.payment.start_transaction", {
      data: { amount },
    })
    .then((result) => {
      return result.data;
    })
    .catch((error) => {
      return error;
    });
};

const DataService = {
  getProductDetail,
  getProduct,
  getCategory,
  getWishlist,
  getCart,
  addToWishlist,
  addToCart,
  moveToCart,
  removeFromWishlist,
  removeFromCart,
  addNewAddress,
  getAllAddress,
  createNewOrder,
  getAssetUrl,
  getTransactionToken,
};

export default DataService;
