import React from "react";
import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <div className="app">
      <footer className="bg-dark" id="tempaltemo_footer">
        <div className="container">
          <div className="row">
            <div className="col-md-3 pt-5">
              <h2 className="h2 text-success pb-3 border-light logo">
                <img
                  src="/assets/images/footer-logo.png"
                  className="card-img-top"
                  alt="..."
                />
              </h2>
              <ul className="list-unstyled text-light footer-link-list footer-contact">
                <li>
                  <span>Phone&nbsp;&nbsp;&nbsp;:</span>
                  <Link className="text-decoration-none" to="/">
                    +75256456411
                  </Link>
                </li>
                <li>
                  <span>Email&nbsp;&nbsp;&nbsp;:</span>
                  <Link className="text-decoration-none" to="/">
                    email@gmail.com
                  </Link>
                </li>
                <li>
                  <span>Address&nbsp;&nbsp;&nbsp;:</span>
                  <Link className="text-decoration-none" to="/">
                    Australia 49 Cumberland Drive, Flagstaff, Hamilton 3219
                  </Link>
                </li>
              </ul>
            </div>
            <div className="col-md-4 pt-5 footer-subscribe">
              <h5 className="h5 text-light pb-3 border-light">Subscribe us</h5>
              <p>
                Be the first to hear about new releases, special offers and
                more.
              </p>
              <div className="input-group mb-2">
                <input
                  type="text"
                  className="form-control bg-dark border-light"
                  id="subscribeEmail"
                  placeholder="Enter your e-mail "
                />
                <div className="input-group-text btn-grey text-light">
                  Submit
                </div>
              </div>
            </div>
            <div className="col-md-3 pt-5">
              <h5 className="h5 text-light pb-3 border-light">Social media</h5>
              <ul className="list-inline text-left footer-icons">
                <li className="list-inline-item border border-light rounded-circle text-center">
                  <a
                    className="text-decoration-none"
                    rel="noreferrer"
                    target="_blank"
                    href="https://www.facebook.com"
                  >
                    <i className="fab fa-facebook-f fa-lg fa-fw"></i>
                  </a>
                </li>
                <li className="list-inline-item border border-light rounded-circle text-center">
                  <a
                    className="text-decoration-none"
                    rel="noreferrer"
                    target="_blank"
                    href="https://www.instagram.com"
                  >
                    <i className="fab fa-instagram fa-lg fa-fw"></i>
                  </a>
                </li>
                <li className="list-inline-item border border-light rounded-circle text-center">
                  <a
                    className="text-decoration-none"
                    rel="noreferrer"
                    target="_blank"
                    href="https://www.twitter.com"
                  >
                    <i className="fab fa-twitter fa-lg fa-fw"></i>
                  </a>
                </li>
                <li className="list-inline-item border border-light rounded-circle text-center">
                  <a
                    className="text-decoration-none"
                    rel="noreferrer"
                    target="_blank"
                    href="https://www.linkedin.com"
                  >
                    <i className="fab fa-linkedin fa-lg fa-fw"></i>
                  </a>
                </li>
              </ul>
            </div>
            <div className="col-md-2 pt-5">
              <h5 className="h5 text-light pb-3 border-light">More</h5>
              <ul className="list-unstyled text-light footer-link-list">
                <li>
                  <Link className="text-decoration-none" to="/register">
                    Sign up
                  </Link>
                </li>
                <li>
                  <Link className="text-decoration-none" to="/aboutus">
                    About Us
                  </Link>
                </li>
                <li>
                  <Link className="text-decoration-none" to="/privacy">
                    Privacy Poilicies
                  </Link>
                </li>
                <li>
                  <Link className="text-decoration-none" to="/cookies">
                    Cookies Policies
                  </Link>
                </li>
                <li>
                  <Link className="text-decoration-none" to="/termsconditions">
                    Terms and Conditions
                  </Link>
                </li>
                <li>
                  <Link className="text-decoration-none" to="/faqs">
                    FAQs
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default Footer;
