import { React, useState, useEffect } from "react";
import "../core/styles.css";
import RenderSmoothImage from "./RenderSmoothImage";
import DataService from "../services/data.service";

export const ChangeAddressModal = (props) => {
  let flag;
  const [address, setAddress] = useState([]);

  useEffect(() => {
    getAddress();
    // eslint-disable-next-line
  }, []);

  const getAddress = () => {
    DataService.getAllAddress().then((data) => {
      //console.log(data)
      setAddress(data.data.message);
      flag = 1;
    });
  };
  if (flag === 1) {
    props.data(address.length > 0 ? address[0] : "", 0);
    flag = 0;
  }

  return (
    <>
      <div className="container">
        <div className="row">
          {address.length > 0 ? (
            address.map((item, i) => (
              <>
                <div key={i} className="col-sm-6">
                  <div className="card">
                    <div className="card-body p-0">
                      <h5 className="card-title">
                        {item.name} <span>{item.location}</span>
                      </h5>
                      <div className="address-text">
                        <p className="f-15 w-100 mt-4">
                          {item.house_no}, {item.area}, {item.city}
                          <br /> {item.state} - {item.pincode}
                          <br />
                        </p>
                      </div>
                      <div className="address-btns">
                        <button>
                          <RenderSmoothImage
                            src="assets/images/icon-edit.png"
                            alt={item.title}
                          />
                        </button>
                        <button>
                          <RenderSmoothImage
                            src="assets/images/icon-trash.png"
                            alt={item.title}
                          />
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </>
            ))
          ) : (
            <div
              className="container-fluid text-center no-padding"
              style={{ padding: "100px" }}
            >
              <div className="col-lg-6 m-auto">
                <p>Address not added yet</p>
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
};
export default ChangeAddressModal;
