import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
import "./App.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Homepage from "./pages/Homepage";
import Bestsellerpage from "./pages/Bestsellerpage";
import Login from "./pages/Login";
import PageNotFound from "./pages/PageNotFound";
import DetailPage from "./pages/productDetail";
import MyBag from "./pages/myBag";
import Address from "./pages/address";
import { RestrictedAccess } from "./private component/RestrictedAccess";
import Register from "./pages/Register";
import CategoryPage from "./pages/CategoryPage";
// import { Profile } from "./pages/Profile";
import { Wishlist } from "./pages/Wishlist";
import MyAccount from "./pages/MyAccount";
import AddAddress from "./pages/AddAddress";
import Orders from "./pages/Orders";
import ChangePassword from "./pages/ChangePassword";
import AboutUs from "./pages/AboutUs";
import Privacy from "./pages/Privacy";
import FAQs from "./pages/FAQs";
import TermsConditions from "./pages/TermsConditions";
import CookiesPolicies from "./pages/CookiesPolicies";

export default function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Homepage />}>
          <Route index element={<Homepage />} />
        </Route>
        <Route exact path="/Bestseller" element={<Bestsellerpage />} />
        <Route
          exact
          path="/category/:item_group"
          element={<CategoryPage />}
        ></Route>
        <Route exact path="/detail/:item_id" element={<DetailPage />} />
        <Route exact path="/Login" element={<Login />} />
        <Route exact path="/register" element={<Register />} />
        <Route path="*" element={<PageNotFound />} />
        <Route path="/aboutus" element={<AboutUs />} />
        <Route path="/privacy" element={<Privacy />} />
        <Route path="/termsconditions" element={<TermsConditions />} />
        <Route path="/cookies" element={<CookiesPolicies />} />
        <Route path="/faqs" element={<FAQs />} />
        <Route element={<RestrictedAccess />}>
          <Route exact path="/mybag" element={<MyBag />} />
          <Route exact path="/wishlist" element={<Wishlist />}></Route>
          <Route exact path="/address" element={<Address />} />
          <Route exact path="/myaccount" element={<MyAccount />}></Route>
          <Route exact path="/addaddress" element={<AddAddress />}></Route>
          <Route exact path="/orders" element={<Orders />}></Route>
          <Route
            exact
            path="/changepassword"
            element={<ChangePassword />}
          ></Route>
        </Route>
      </Routes>
    </BrowserRouter>
  );
}
