import React, { Fragment, useState } from "react";
import SimilarProducts from "../../core/SimilarProducts";
import ProductDetails from "../../core/ProductDetails";
import { Carousel } from "react-carousel-minimal";
import { useEffect } from "react";
import DataService from "../../services/data.service";

const Content = (props) => {
  const [details, setDetails] = useState('');
  const [imageData, setImageData] = useState([]);

  useEffect(() => {
    getDetail();
    // eslint-disable-next-line 
  }, [props.productId]);

  const getDetail = () => {
    DataService.getProductDetail(props.productId)
      .then((data) => {
        //console.log(data)
        setDetails(data.data.message)
        setImageData(data.data.message.image)
      })
  }
  const captionStyle = {
    fontSize: "2em",
    fontWeight: "bold",
  };
  const slideNumberStyle = {
    fontSize: "20px",
    fontWeight: "bold",
  };

  return (
    <Fragment>
      <section className="container-fluid py-4">
        <div className="row">
          <div className="u-line section-padding">
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-6 col-lg-7">
                  <div className="row">
                    <div className="col-12 mb-4">
                      <div className=" py-5 px-4 prod-box alignProductImage">

                        <img
                          className=""
                          src={imageData.length > 0 ? imageData[0].image : ""}
                          alt="product"
                        />
                      </div>
                    </div>
                    <div className="col-md-6 col-6 mb-4">
                      <div className=" py-5 px-4 prod-box alignProductImage min-height-box">
                        <img
                          className=""
                          src={imageData.length > 1 ? imageData[1].image : ""}
                          alt="product"
                        />
                      </div>
                    </div>

                    <div className="col-md-6 col-6 mb-4">
                      <div className=" py-5 px-4 prod-box alignProductImage min-height-box">
                        <img
                          className=""
                          src={imageData.length > 2 ? imageData[2].image : ""}
                          alt="product"
                        />
                      </div>
                    </div>

                    <div className="col-md-6 col-6 mb-4">
                      <div className=" py-5 px-4 prod-box alignProductImage min-height-box">
                        <img
                          className=""
                          src={imageData.length > 3 ? imageData[3].image : ""}
                          alt="product"
                        />
                      </div>
                    </div>
                    <div className="col-md-6 col-6 mb-4">
                      <div className=" py-5 px-4 prod-box alignProductImage min-height-box relative">
                        <img
                          className=""
                          src={imageData.length > 3 ? imageData[3].image : ""}
                          alt="product"
                        />
                        <div
                          className="overlap cursor-pointer"
                          data-bs-toggle="modal"
                          data-bs-target="#ImageSlider"
                        >
                          More
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-md-6 col-lg-5 pl-90 productDetails">
                  <ProductDetails details={details} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="container py-5">
        <div className="row">
          <SimilarProducts details={details.similar_products} />
        </div>
      </section>
      {imageData.length ?
        <div
          className="modal fade"
          id="ImageSlider"
          tabIndex="-1"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
              <div className="modal-body">
                <Carousel
                  data={imageData}
                  width="auto"
                  height="500px"
                  captionStyle={captionStyle}
                  radius="10px"
                  slideNumber={true}
                  slideNumberStyle={slideNumberStyle}
                  captionPosition="bottom"
                  automatic={true}                  
                  thumbnails={true}
                  thumbnailWidth="100px"
                  style={{
                    textAlign: "center",
                    maxHeight: "auto",
                    margin: "2px auto",
                  }}
                />
              </div>
            </div>
          </div>
        </div>
        : ""
      }
    </Fragment>
  );
};

export default Content;
