import React, { Fragment } from "react";
//import Category from "../../core/Category";
import Product from "../../core/Product";
import BestSeller from "../../core/BestSeller";
import { useEffect } from "react";
import { useState } from "react";
import DataService from "../../services/data.service";
import OnSale from "../../core/OnSale";
import categories from "../../data/category.json";
import RenderSmoothImage from "../../core/RenderSmoothImage";
import { Link } from "react-router-dom";

const Content = () => {
  const [newArrival, setNewarrival] = useState([]);
  const [bestselling, setBestselling] = useState([]);
  const [onSale, setOnsale] = useState([]);
  //const [category, setCategory] = useState([]);

  useEffect(() => {
    getProducts();
  }, []);

  const getProducts = () => {
    DataService.getProduct().then((data) => {
      //console.log(data)
      setNewarrival(data.data.message.new_arrival_items);
      setBestselling(data.data.message.best_selling_products);
      setOnsale(data.data.message.on_sale_products);
    });
  };

  return (
    <Fragment>
      <div className="container-fluid text-center no-padding black-bg">
        <div className="col-lg-6 m-auto">
          <h3 className="h3">CATEGORIES</h3>
        </div>
      </div>
      <section className="container py-5">
        <div className="row">
          {/*<Category item_group={"Women"} start={0} page_length={10} />*/}
          {categories.map((item, i) => (
            <div key={i} className="col-sm-12 col-md-4  p-5 prod-box no-bg">
              <h2 className="h5 text-center mt-3 mb-3">{item.category} </h2>
              <Link
                to={"/category/" + item.category}
                className="text-decoration-none"
              >
                <RenderSmoothImage
                  src={`/${item.img}`}
                  alt={item.title}
                  className="img-fluid"
                />
              </Link>
              <h2 className="h5 text-center mt-3 mb-3">{item.title} </h2>
              <p className="text-center">
                {item.desc} <br></br>
                <a href={"/category/" + item.category} className="shop-now">
                  Shop Now
                </a>
              </p>
            </div>
          ))}
        </div>
      </section>
      {newArrival.length > 0 ? (
        <>
          <div className="container-fluid text-center no-padding black-bg">
            <div className="col-lg-6 m-auto">
              <h3 className="h3">NEW ARRIVAL</h3>
            </div>
          </div>
          <section className="container py-5">
            <div className="row">
              <Product newArrival={newArrival} />
            </div>
          </section>
        </>
      ) : (
        ""
      )}
      {bestselling.length > 0 ? (
        <>
          <div className="container-fluid text-center no-padding black-bg">
            <div className="col-lg-6 m-auto">
              <h3 className="h3">best seller</h3>
            </div>
          </div>
          <section className="container py-5">
            <div className="row">
              <BestSeller bestselling={bestselling} />
            </div>
          </section>
        </>
      ) : (
        ""
      )}
      {onSale.length > 0 ? (
        <>
          <div className="container-fluid text-center no-padding black-bg">
            <div className="col-lg-6 m-auto">
              <h3 className="h3">on sale</h3>
            </div>
          </div>
          <section className="container py-5">
            <div className="row">
              <OnSale onSale={onSale} />
            </div>
          </section>
        </>
      ) : (
        ""
      )}
    </Fragment>
  );
};

export default Content;
