import React, { Fragment, useEffect, useState } from "react";
import Pricedetail from "../productprice/pricedetail";
import { ChangeAddressModal } from "../address/changeAddressModal";
import { Link, useNavigate } from "react-router-dom";
import DataService from "../../services/data.service";
import AddressModal from "../address/addressModal";
import Snackbar from "@mui/material/Snackbar";
import Alert from "@mui/material/Alert";
import cookie from "js-cookie";

const Content = (props) => {
  const auth = cookie.get("user");
  const [cart, setCart] = useState([]);
  //const [address, setAddress] = useState([]);
  const [loading, setLoading] = useState(true);
  const [checked, setChecked] = useState([]);
  const [allChecked, setAllChecked] = useState(true);
  const [selectedCount, setSelectedCount] = useState(0);
  const [selectedcart, setSelectedcart] = useState([]);
  const [removeLoading, setRemoveLoading] = useState(false);
  const [removeSuccess, setRemoveSuccess] = useState(false);
  const [nothingSelected, setNothingSelected] = useState(false);

  const [selectedAddress, setSelectedAddress] = useState([]);
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [inWishlistFlag, setInWishlistFlag] = useState(0);
  const [successShow, setSuccessShow] = useState(false);
  const [rejectShow, setRejectShow] = useState(false);
  const [successCartShow, setSuccessCartShow] = useState(false);
  const [rejectCartShow, setRejectCartShow] = useState(false);

  const handleClose = () => {
    setRemoveSuccess(false);
    setNothingSelected(false);
    setSuccessShow(false);
    setRejectShow(false);
    setSuccessCartShow(false);
    setRejectCartShow(false);
  };
  let sizeId = window.location.href.split("#")[1];
  let discounted_price;
  let original_price;
  let discount;

  const navigate = useNavigate();

  const handleCheckboxChange = (i) => {
    let selectedProducts = [];
    const updatedCheckState = checked.map((item, index) =>
      i === index ? !item : item
    );
    setChecked(updatedCheckState);

    const count = updatedCheckState.reduce((sum, currentState) => {
      if (currentState === true) {
        return sum + 1;
      }
      return sum;
    }, 0);
    setSelectedCount(count);
    if (count < cart.length) {
      setAllChecked(false);
    }
    if (count === cart.length) {
      setAllChecked(true);
    }

    updatedCheckState.map((item, i) => {
      if (item) {
        if (cart[i].out_of_stock === 0) {
          selectedProducts.push(cart[i]);
        }
      }
      return 1;
    });
    setSelectedcart(selectedProducts);
  };

  const handleAllCheckedChange = () => {
    let status = !allChecked;
    setAllChecked(status);
    let selectedProducts = [];
    let updatedCheckState;
    if (status) {
      updatedCheckState = new Array(cart.length).fill(true);
      setChecked(updatedCheckState);
    } else {
      updatedCheckState = new Array(cart.length).fill(false);
      setChecked(updatedCheckState);
    }
    const count = updatedCheckState.reduce((sum, currentState) => {
      if (currentState === true) {
        return sum + 1;
      }
      return sum;
    }, 0);
    setSelectedCount(count);
    if (count === 0) {
      setAllChecked(false);
    }
    if (count === cart.length) {
      setAllChecked(true);
    }

    updatedCheckState.map((item, i) => {
      if (item) {
        if (cart[i].out_of_stock === 0) {
          selectedProducts.push(cart[i]);
        }
      }
      return 1;
    });
    setSelectedcart(selectedProducts);
  };

  const selectAddress = (address, index) => {
    if (
      address.pincode &&
      address.area &&
      address.house_no &&
      address.city &&
      address.state
    ) {
      setSelectedAddress(address);
    }
    setSelectedIndex(index);
  };

  useEffect(() => {
    getCart();
    // eslint-disable-next-line
  }, []);

  const getCart = () => {
    DataService.getCart().then((data) => {
      //console.log(data)
      let products = data.data.message;
      setCart(data.data.message);
      setSelectedcart(data.data.message);
      setChecked(new Array(products.length).fill(true));
      setAllChecked(true);
      setSelectedCount(products.length);
      setLoading(false);
    });
  };

  const add_to_wishlist = () => {
    if (auth) {
      setLoading(true);
      DataService.addToWishlist(
        props.item_code,
        sizeId,
        discounted_price,
        original_price,
        discount
      ).then((data) => {
        if (data.data.message.Wishlist) {
          if (data.data.message.Wishlist.success_key === 1) {
            setSuccessShow(true);
            setLoading(false);
            setInWishlistFlag(1);
          }
        }
        if (data.data.message.success_key === 1) {
          setSuccessShow(true);
          setLoading(false);
          setInWishlistFlag(1);
        }
        if (data.data.message.success_key === 0) {
          setRejectShow(true);
          setLoading(false);
          setInWishlistFlag(1);
        }
      });
    } else {
      navigate("/login");
    }
  };
  const removeProduct = () => {
    let toBeRemoved = [];
    checked.map((item, i) => {
      if (item) {
        toBeRemoved.push({
          item_code: cart[i].item_code,
        });
      }
      return 1;
    });
    if (toBeRemoved.length > 0) {
      setRemoveLoading(true);
      DataService.removeFromCart(toBeRemoved).then((data) => {
        if (data.data.message.success === 1) {
          setRemoveLoading(false);
          setRemoveSuccess(true);
          getCart();
        }
      });
    } else {
      setNothingSelected(true);
    }
  };

  return (
    <Fragment>
      <Snackbar
        open={successShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="success"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "4px",
              opacity: "1",
            },
          }}
          style={{
            background: "green",
            color: "white",
          }}
        >
          <b>Successfully Added to Wishlist</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={rejectShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="info"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "3px",
              opacity: "1",
            },
          }}
          style={{
            background: "black",
            color: "white",
            fontWeight: "500",
          }}
        >
          <b>Already present in Wishlist</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={successCartShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="success"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "4px",
              opacity: "1",
            },
          }}
          style={{
            background: "green",
            color: "white",
          }}
        >
          <b>Successfully Added to Bag</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={rejectCartShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="info"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "3px",
              opacity: "1",
            },
          }}
          style={{
            background: "black",
            color: "white",
            fontWeight: "500",
          }}
        >
          <b>Already present in Bag</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={removeSuccess}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "10%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="success"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "4px",
              opacity: "1",
            },
          }}
          style={{
            background: "black",
            color: "white",
          }}
        >
          <b>Selected Items Removed from Bag</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={nothingSelected}
        autoHideDuration={3000}
        onClose={handleClose}
        sx={{ height: "38%" }}
        anchorOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
        message="Select any item to remove from bag"
      ></Snackbar>
      <div id="checkoutUi" className="container max-1200 min-height">
        <div className="row">
          <div className="col my-5">
            <ul className="checkoutsteps">
              <li className="active steps">BAG</li>
              <li className="steps">ADDRESS</li>
              {/*<li className="steps">PAYMENT </li>*/}
            </ul>
          </div>
          <div className="col-lg-8">
            <div className="card mb-3">
              {cart.length > 0 ? (
                <div
                  className="addressStrip-base-desktopContainer"
                  data-bs-toggle="modal"
                  data-bs-target="#changeAddress"
                >
                  {selectedAddress.pincode ? (
                    <>
                      <div className="addressStrip-base-title">
                        <div className="addressStrip-base-addressName">
                          <b>Deliver to:</b>
                          <div className="addressStrip-base-highlight">
                            {" "}
                            {selectedAddress.pincode}
                          </div>
                        </div>
                        <div className="addressStrip-base-subText">
                          {selectedAddress.house_no}, {selectedAddress.area},{" "}
                          {selectedAddress.city}, {selectedAddress.state}
                        </div>
                      </div>
                      <button
                        className="btn btn-secondary"
                        data-bs-toggle="modal"
                        data-bs-target="#changeAddress"
                      >
                        CHANGE ADDRESS
                      </button>
                    </>
                  ) : (
                    <>
                      <div className="addressStrip-base-title">
                        <div className="addressStrip-base-addressName">
                          <h6 className="mb-0">Select Delivery Address</h6>
                        </div>
                      </div>
                      <button
                        className="btn btn-secondary"
                        data-bs-toggle="modal"
                        data-bs-target="#changeAddress"
                      >
                        SELECT ADDRESS
                      </button>
                    </>
                  )}
                </div>
              ) : (
                <div
                  className="card-body px-xs-0 d-flex justify-content-between align-items-center"
                  data-bs-toggle="modal"
                  data-bs-target="#addAddress"
                  style={{ cursor: "pointer" }}
                >
                  <h6 className="mb-0">Add your home address</h6>
                  <button
                    className="btn btn-secondary"
                    data-bs-toggle="modal"
                    data-bs-target="#addAddress"
                  >
                    ADD ADDRESS
                  </button>
                </div>
              )}
            </div>
            {/*<div className="card border-start-0 border-top-0 border-end-0 rounded-0 pb-4 ">
              <div className="card-header border-0 p-0 no-bg"><h5>DELIVERY SERVICE </h5></div>
              <div className="card-body px-xs-0 d-flex justify-content-between align-items-center">
                <input type="text" className="pincode" placeholder="Enter Pincode" />
                <button className="btn btn-secondary">CHECK PIN CODE</button>
              </div>
            </div>*/}
            <div className="card border-0">
              <div className="card-body d-flex justify-content-between align-items-center py-4 my-1">
                <p className="m-0 f-20 f-500 customCheck">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    checked={allChecked}
                    onChange={handleAllCheckedChange}
                  />{" "}
                  {selectedCount}/{cart.length} Items Selected
                </p>
                <span className="itemsAction">
                  <Link
                    to=" "
                    className="text-danger f-16 f-500 no-decoration"
                    onClick={removeProduct}
                  >
                    {removeLoading && (
                      <span className="spinner-border spinner-border-sm"></span>
                    )}
                    Remove
                  </Link>
                  {inWishlistFlag === 0 ? (
                    <button
                      className="f-16 f-500 no-decoration gray3"
                      onClick={add_to_wishlist}
                    >
                      {loading && (
                        <span className="spinner-border spinner-border-sm"></span>
                      )}{" "}
                      <span>Move To Wishlist</span>
                    </button>
                  ) : (
                    <button disabled className="f-16 f-500 no-decoration gray3">
                      <span>Wishlist</span>
                    </button>
                  )}
                </span>
              </div>
            </div>
            <div className="container-fluid text-center no-padding">
              <div className="col-lg-6 m-auto">
                {loading && (
                  <div
                    className="container-fluid text-center no-padding"
                    style={{ padding: "20%" }}
                  >
                    <div className="col-lg-6 m-auto">
                      <span className="spinner-border spinner-border-sm"></span>
                    </div>
                  </div>
                )}
              </div>
            </div>
            {cart.length > 0
              ? cart.map((item, i) => (
                  <div className="card mb-3">
                    <div className="card-body d-flex flex-wrap">
                      <div className="product-thumb">
                        <input
                          className="form-check-input"
                          type="checkbox"
                          checked={checked[i]}
                          onChange={() => handleCheckboxChange(i)}
                        />
                        <Link
                          to={
                            "/detail/" +
                            item.item_code.split("-")[0] +
                            "#" +
                            item.item_code.split("-")[1]
                          }
                          className="text-decoration-none"
                        >
                          <img
                            className="img-fluid"
                            src={item.image}
                            alt={item.item_code}
                          />
                        </Link>
                      </div>
                      <div className="product-details-checkout">
                        <Link
                          to={
                            "/detail/" +
                            item.item_code.split("-")[0] +
                            "#" +
                            item.item_code.split("-")[1]
                          }
                          className="text-decoration-none"
                        >
                          <h5>{item.item_code}</h5>
                        </Link>
                        {item.out_of_stock !== 0 ? (
                          <p>
                            <span className="out-of-stock">(Out of Stock)</span>
                          </p>
                        ) : (
                          ""
                        )}
                        <div className="dimensions">
                          <div className="selectDim me-2">
                            Size: {item.item_code.split("-")[1]}
                          </div>
                          <div className="selectDim">Qty: 1</div>
                        </div>
                        {item.discount_amount === 0 ? (
                          <h4 className="mt-3 d-flex align-items-center f-16">
                            Rs. {item.discounted_price}
                          </h4>
                        ) : (
                          <h4 className="mt-3 d-flex align-items-center f-16">
                            Rs. {item.discounted_price}
                            <span className="oldPrice line-through f-14">
                              Rs. {item.price}
                            </span>
                            <span className="discountPrice f-16">
                              (Rs. {item.discount_amount} OFF)
                            </span>
                          </h4>
                        )}

                        <button className="closePosition">
                          <Link
                            to=" "
                            className="text-danger f-16 f-500 no-decoration"
                            onClick={removeProduct}
                          >
                            {removeLoading && (
                              <span className="spinner-border spinner-border-sm"></span>
                            )}
                            <i>
                              <svg
                                width="20"
                                height="19"
                                viewBox="0 0 20 19"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                              >
                                <path
                                  d="M18.5 1.5L1.5 18"
                                  stroke="black"
                                  strokeWidth="1.5"
                                  strokeLinecap="round"
                                />
                                <path
                                  d="M1 1.5L19 18"
                                  stroke="black"
                                  strokeWidth="1.5"
                                  strokeLinecap="round"
                                />
                              </svg>
                            </i>
                          </Link>
                        </button>
                      </div>
                    </div>
                  </div>
                ))
              : !loading && (
                  <div
                    className="container-fluid text-center no-padding"
                    style={{ padding: "100px" }}
                  >
                    <div className="col-lg-6 m-auto">
                      <p>YOUR CART IS EMPTY</p>
                      <Link
                        to="/wishlist"
                        className="btn btn-primary me-4 grow6"
                      >
                        ADD ITEMS FROM WISHLIST
                      </Link>
                    </div>
                  </div>
                )}
          </div>
          <div className="col-lg-4">
            <Pricedetail
              cart={selectedcart.length > 0 ? selectedcart : ""}
              index={selectedIndex}
            />
          </div>
        </div>
      </div>
      <ChangeAddressModal data={selectAddress} />
      <AddressModal />
    </Fragment>
  );
};

export default Content;
