import React from "react";
import RenderSmoothImage from "./RenderSmoothImage";
import "../core/styles.css";
import { Link } from "react-router-dom";

const OnSale = (onSale) => {
  return (
    <section className="u-line section-padding">
      <div className="container">
        <div className="row">
          {onSale?.onSale?.length > 0 ?
            onSale.onSale.map((item, i) => (
              <div key={i} className="col-12 col-md-4 mt-3">
                <div className=" p-5 prod-box">
                  <Link to={'/detail/' + item.id + '#' + item.title.split('-')[1]}
                    className="text-decoration-none"
                  >
                    <RenderSmoothImage
                      src={item.image}
                      alt={item.title}
                      className="img-fluid"
                    />
                  </Link>
                  <h2 className="h5 text-center mt-3 mb-3">{item.title}</h2>
                  <p className="text-center">
                    {item.description} <br></br>
                    <span className="price">Rs. {item.price}</span>
                  </p>
                </div>
              </div>
            )) : ""}
        </div>
      </div>
    </section>
  );
};
export default OnSale;
