import React, { Fragment } from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
import DataService from "../../services/data.service";
import PricedetailPlaceOrder from "../productprice/pricedetail_placeorder";
import AddressModal from "./addressModal";

const Content = (props) => {
  const [address, setAddress] = useState([]);
  const [loading, setLoading] = useState(true);
  //console.log(props)

  useEffect(() => {
    getAddress();
  }, [])

  const getAddress = () => {
    DataService.getAllAddress()
      .then((data) => {
        //console.log(data)
        setAddress(data.data.message)
        setLoading(false)
      })
  }

  return (
    <Fragment>
      <div id="checkoutUi" className="container max-1200 min-height">
        <div className="row">
          <div className="col my-5">
            <ul className="checkoutsteps">
              <Link to="/mybag" className="text-decoration-none">
                <li className="steps">BAG</li>
              </Link>
              <li className="active steps">ADDRESS</li>
              {/*<li className="steps">PAYMENT </li>*/}
            </ul>
          </div>
          <div className="col-lg-8">
            <h4>Select Delivey Address</h4>
            <h6 className="mt-3 mb-3">Default Address</h6>
            {/*
            <div className="card mb-3">
              <div className="card-body d-flex flex-wrap">
                <input class="form-check-input radio-position cus-radio" name="address" type="radio" />
                <div className="w-100 ms-5">
                  <h5 className="f-17">Ashish <span className="tag-text ms-2 text-uppercase">Work</span></h5>
                  <p className="f-15 w-100 mt-4">#123 house number 1 mohali peer baba Road, Mohali, Punjab, 140607<br />
                    Mobile number : 9893101977</p>
                  <p className="w-100 mt-2"><button className="btn btn-outline-dark text-uppercase me-3 radius-3 f-500">Remove</button><button className="btn btn-outline-dark text-uppercase radius-3 f-500">Edit</button></p>
                </div>
              </div>
            </div>*/}
            <div className="container-fluid text-center no-padding">
              <div className="col-lg-6 m-auto">
                {loading && (
                  <span className="spinner-border spinner-border-sm"></span>
                )}
              </div>
            </div>
            {
              address.length > 0 ?
                address.map((item, i) =>
                  <>
                    <div>
                      {
                        i === 1 ?
                          <h6 className="mt-3 mb-3">Other Address</h6> : ""
                      }
                    </div>
                    <div className="card mb-3">
                      <div className="card-body d-flex flex-wrap">
                        {
                          i === props.index ?
                            <input defaultChecked className="form-check-input radio-position cus-radio" name="address" type="radio" />
                            :
                            <input className="form-check-input radio-position cus-radio" name="address" type="radio" />
                        }
                        <div className="w-100 ms-5">
                          <h5 className="f-17"><span className="tag-text ms-2 text-uppercase">Home</span></h5>
                          <p className="f-15 w-100 mt-4">{item.house_no}, {item.area} <br /> {item.city}, {item.state} - {item.pincode}<br />
                          </p>
                          <p className="w-100 mt-2">
                            <button className="btn btn-outline-dark text-uppercase me-3 radius-3 f-500">
                              Remove
                            </button>
                            <button className="btn btn-outline-dark text-uppercase radius-3 f-500">
                              Edit
                            </button>
                          </p>
                        </div>
                      </div>
                    </div>
                  </>
                )
                :
                !loading &&
                (<div className="container-fluid text-center no-padding" style={{ padding: '100px' }}>
                  <div className="col-lg-6 m-auto">
                    <p>Address not added yet</p>
                  </div>
                </div>)
            }
            <div className="card mb-3">
              <div className="card-body px-xs-0 d-flex justify-content-between align-items-center">
                <h6 className="mb-0">Add New Address</h6>
                <button className="btn btn-secondary" data-bs-toggle="modal" data-bs-target="#addAddress">ADD ADDRESS</button>
              </div>
            </div>
          </div>
          <div className="col-lg-4">
            <PricedetailPlaceOrder cart={props.data.length > 0 ? props.data : ""} />
          </div>
        </div>
      </div>
      <AddressModal />
    </Fragment>
  );
};

export default Content;
