import React, { Fragment } from "react";
import { useEffect } from "react";
import { useState } from "react";
import DataService from "../../services/data.service";

const PricedetailPlaceOrder = (props) => {
  let price = 0;
  let discountedPrice = 0;
  let discountAmount = 0;
  //const couponDiscount = 0;
  const [asset, setAsset] = useState("");
  const [loading, setLoading] = useState(false);
  let convenienceFee = 0;
  let date = new Date();
  date.setDate(date.getDate() + 5);

  useEffect(() => {
    getAsset();
  }, []);

  const getAsset = () => {
    DataService.getAssetUrl()
      .then((data) => {
        //console.log(data)
        setAsset(data.data.message)
      })
  }

  if (props.cart.length > 0) {
    props.cart.forEach(item => {
      if (item.out_of_stock === 0) {
        price = price + item.price;
        discountedPrice = discountedPrice + item.discounted_price;
        discountAmount = discountAmount + item.discount_amount;
      }
    })
    if (discountedPrice < 599) {
      convenienceFee = 50;
    }
  }

  const setup_gravity_payment = () => {
    const script = document.createElement('script');

    script.src = asset + "/cip-hosted-modal.js";
    script.async = true;

    document.body.appendChild(script);
    // eslint-disable-next-line no-undef
    setTimeout(() => emergepay.init(), 50)
    setLoading(true);
    const totalAmount = discountedPrice + convenienceFee;
    DataService.getTransactionToken(totalAmount)
      .then(data => {
        setLoading(false)
        // eslint-disable-next-line no-undef
        emergepay.open({
          transactionToken: data.message.transactionToken,
          onTransactionSuccess: (approvalData) => {
            console.log("Transaction Successful")
            // eslint-disable-next-line no-undef
            emergepay.close();
            setLoading(false);
          },

          onTransactionFailure: (failureData) => {
            console.log("Transaction Failed");
            setLoading(false);
            const message = "Transaction Failed";
            alert({ message, indicator: "red" });
            return;
          },

          onTransactionCancel: () => {
            console.log("Transaction Cancelled");
            setLoading(false);
            const message = "You cancelled transaction.";
            alert({ message, indicator: "red" });
            return;
          }
        });
      })
  }

  return (
    <Fragment>
      <div className="card border-start-0 border-top-0 border-end-0 rounded-0 pb-4">
        <h6>Delivery Estimates</h6>
        <p className="f-16 text-gray4">
          Estimated delivey by
          <span className="f-500 text-back">
            <b> {JSON.stringify(date.toString()).substring(1, 16)} </b>
          </span>
        </p>
      </div><br />
      {/* <div className="card border-start-0 border-top-0 border-end-0 rounded-0 pb-4">
        <div className="card-header border-0 p-0 no-bg"><h5>Coupons</h5></div>
        <div className="card-body d-flex justify-content-between align-items-center">
          <span className="d-flex align-items-center"><i className="mr-2">
            <svg width="20" viewBox="0 0 23 23" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M1 1H10.7929C10.9255 1 11.0527 1.05268 11.1464 1.14645L21.1464 11.1464C21.3417 11.3417 21.3417 11.6583 21.1464 11.8536L11.8623 21.1377C11.6636 21.3364 11.3404 21.3324 11.1467 21.129L1.62752 11.1339C1.54523 11.0475 1.49655 10.9345 1.49028 10.8153L1 1.5" stroke="black" strokeWidth="1.5" strokeLinecap="round" />
              <circle cx="7" cy="6" r="1.5" fill="#D0EDFF" />
            </svg></i>
            <input type="text" className="pincode couponcode ms-2" placeholder="Enter coupon code"
              value={coupon} onChange={e => setCoupon(e.target.value)} />
          </span>
          {
            coupon === "" ?
              <button className="btn btn-secondary" data-bs-toggle="modal" data-bs-target="#applyCoupon">
                <span>APPLY</span>
              </button>
              :
              <button className="btn btn-secondary" onClick={removeCoupon}>
                <span>REMOVE</span>
              </button>
          }
        </div>
      </div> */}
      <div className="card border-0">
        <div className="card-body py-4 my-1">
          <p className="m-0 f-16 f-500 customCheck text-black">Price Details ({props.cart.length} items)</p>
          <div className="row">
            <div className="col-12 d-flex justify-content-between f-18 mb-2 mt-3">
              <span>Total MRP</span><span className="f-500 ">Rs {price}</span>
            </div>
            <div className="col-12 d-flex justify-content-between mb-2 f-18">
              <span>Discount on MRP</span><span className="f-500 text-green">-Rs {discountAmount}</span>
            </div>
            {/*<div className="col-12 d-flex justify-content-between mb-2 f-18">
              <span>Coupon Discount</span><span className="f-500 text-green">-Rs {couponDiscount}</span>
            </div>*/}
            <div className="col-12 d-flex mb-2 f-18">
              <span>Convenience Fee</span>
              <span className="f-500 ms-auto">Rs</span>
              <span className="line-through f-500">99</span>
              <span className="f-500 text-green ms-1">
                {
                  convenienceFee === 0 ?
                    "Free"
                    :
                    "Rs " + convenienceFee
                }
              </span>
            </div>
            <div className="col-12 d-flex justify-content-between align-items-center py-4 my-1 border-top">
              <p className="m-0 f-18 f-500 customCheck text-black">Total Amount</p>
              <span className="itemsAction f-18 f-500">Rs {discountedPrice + convenienceFee}</span>
            </div>
            <div className="col-12">
              {
                props.cart.length > 0 ?
                  <button onClick={setup_gravity_payment} className="btn btn-primary w-100" disabled={loading}>
                    {loading && (
                      <span className="spinner-border spinner-border-sm"></span>
                    )} {" "}
                    <span>CONTINUE</span>
                  </button>
                  // <button onClick={setup_gravity_payment} className="btn btn-primary w-100">CONTINUE</button>
                  :
                  <button disabled className="btn btn-primary w-100">CONTINUE</button>
              }
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default PricedetailPlaceOrder;