import React, { Fragment } from "react";
import { useState } from "react";
import Category from "../../core/Category";

const Content = (props) => {
  const [start, setStart] = useState(0);
  const [pageLength, setPagelength] = useState(10);

  const [players, setPlayers] = useState(["a", "b", "c", "d", "e", "f"]);

  const sortPlayers = (selectEvent) => {
    const options = {
      "a-z": [...players].sort((a, b) => (a < b ? -1 : 1)),
      "z-a": [...players].sort((a, b) => (a < b ? 1 : -1)),
    };

    setPlayers(options[selectEvent.target.value]);
  };

  return (
    <Fragment>
      <div className="container-fluid text-center no-padding black-bg">
        <div className="col-lg-6 m-auto">
          <h3 className="h3">{props.group}</h3>
        </div>
        <div className="input-container">
          <ul className="cont-start-page">
            <li>
              <strong>Start : </strong>
              <input
                className="input-start"
                type="number"
                value={start}
                onChange={(e) => setStart(e.target.value)}
                min={0}
              />
            </li>

            <li>
              <strong>Page Length : </strong>
              <input
                className="input-pageLength"
                type="number"
                value={pageLength}
                onChange={(e) => setPagelength(e.target.value)}
                min={1}
              />
            </li>
            <li>
              <select onChange={sortPlayers}>
                <option value="a-z">A - Z</option>
                <option value="z-a">Z - A</option>
              </select>
              {players.map((player, index) => (
                <p key={index}>{player}</p>
              ))}

              {players.map((player, index) => {
                const playerNum = `${player}${index}`;
                return (
                  <p key={index}>
                    {player}
                    {playerNum}
                  </p>
                );
              })}
            </li>
          </ul>
        </div>
      </div>
      <section className="container py-5">
        <div className="row">
          <Category
            item_group={props.group}
            start={start}
            page_length={pageLength}
            players={players}
          />
        </div>
      </section>
    </Fragment>
  );
};

export default Content;
