import React, { useState } from "react";
import RenderSmoothImage from "./RenderSmoothImage";
import "../core/styles.css";
import { useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import DataService from "../services/data.service";
import cookie from "js-cookie";
import Snackbar from "@mui/material/Snackbar";
import Alert from "@mui/material/Alert";

const Category = (props) => {
  const [categoryProducts, setCategoryProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const auth = cookie.get("user");

  const [inWishlistFlag, setInWishlistFlag] = useState(0);

  const [successShow, setSuccessShow] = useState(false);
  const [rejectShow, setRejectShow] = useState(false);

  const handleClose = () => {
    setSuccessShow(false);
    setRejectShow(false);
  };

  let sizeId = window.location.href.split("#")[1];
  let discounted_price;
  let original_price;
  let discount;

  const navigate = useNavigate();

  const add_to_wishlist = () => {
    if (auth) {
      setLoading(true);
      DataService.addToWishlist(
        props.item_code,
        sizeId,
        discounted_price,
        original_price,
        discount
      ).then((data) => {
        if (data.data.message.Wishlist) {
          if (data.data.message.Wishlist.success_key === 1) {
            setSuccessShow(true);
            setLoading(false);
            setInWishlistFlag(1);
          }
        }
        if (data.data.message.success_key === 1) {
          setSuccessShow(true);
          setLoading(false);
          setInWishlistFlag(1);
        }
        if (data.data.message.success_key === 0) {
          setRejectShow(true);
          setLoading(false);
          setInWishlistFlag(1);
        }
      });
    } else {
      navigate("/login");
    }
  };

  useEffect(() => {
    getCategories();
    // eslint-disable-next-line
  }, [props.start, props.page_length]);

  const getCategories = () => {
    DataService.getCategory(
      props.item_group ? props.item_group : "Men",
      props.start ? props.start : 0,
      props.page_length ? props.page_length : 10
    ).then((data) => {
      //console.log(data)
      setCategoryProducts(data.data.message.products);
      setLoading(false);
    });
  };

  return (
    <>
      <Snackbar
        open={successShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="success"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "4px",
              opacity: "1",
            },
          }}
          style={{
            background: "green",
            color: "white",
          }}
        >
          <b>Successfully Added to Wishlist</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={rejectShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="info"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "3px",
              opacity: "1",
            },
          }}
          style={{
            background: "black",
            color: "white",
            fontWeight: "500",
          }}
        >
          <b>Already present in Wishlist</b>
        </Alert>
      </Snackbar>
      <div className="container-fluid text-center no-padding black-bg">
        <div className="col-lg-6 m-auto">
          {loading && (
            <div
              className="container-fluid text-center no-padding"
              style={{ padding: "10%" }}
            >
              <div className="col-lg-6 m-auto">
                <span className="spinner-border spinner-border-sm"></span>
              </div>
            </div>
          )}
        </div>
      </div>
      <section className="browse-cat u-line section-padding">
        <div className="container">
          <div className="row">
            {categoryProducts.length > 0
              ? categoryProducts.map((item, i) => (
                  <div key={i} className="col-12 col-md-4 mt-3">
                    <div className="p-5 prod-box">
                      {inWishlistFlag === 0 ? (
                        <button
                          className="btn fa fa-heart wishlist-heart"
                          onClick={add_to_wishlist}
                        >
                          {loading && (
                            <span className="spinner-border spinner-border-sm"></span>
                          )}{" "}
                          <span>ADD TO WISHLIST</span>
                        </button>
                      ) : (
                        <button
                          disabled
                          className="btn fa fa-heart wishlist-heart-active"
                        >
                          <span>WISHLISTED</span>
                        </button>
                      )}
                      <Link
                        to={
                          "/detail/" +
                          item.id +
                          "#" +
                          item.description.split("-")[1]
                        }
                        className="text-decoration-none"
                      >
                        <RenderSmoothImage
                          src={`/${item.img}`}
                          alt={item.title}
                          className="img-fluid"
                        />
                      </Link>
                      <h2 className="h5 text-center mt-3 mb-3">{item.title}</h2>
                      <p className="text-center">
                        {item.desc} <br></br>
                        <span className="price">Rs. {item.original_price}</span>
                      </p>
                    </div>
                  </div>
                ))
              : !loading && (
                  <div
                    className="container-fluid text-center no-padding"
                    style={{ padding: "100px" }}
                  >
                    <div className="col-lg-6 m-auto">
                      <p>Nothing to show here yet</p>
                    </div>
                  </div>
                )}
          </div>
        </div>
      </section>
    </>
  );
};

export default Category;
