import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Fragment } from "react";
import { Link } from "react-router-dom";
import DataService from "../../services/data.service";
import RenderSmoothImage from "../../core/RenderSmoothImage";
import Snackbar from "@mui/material/Snackbar";
import Alert from "@mui/material/Alert";
import { SelectSizeModal } from "./selectSizeModal";

export const Content = () => {
  const [wishlist, setWishlist] = useState([]);
  const [loading, setLoading] = useState(true);
  const [cartLoading, setCartLoading] = useState(false);
  const [successShow, setSuccessShow] = useState(false);
  const [rejectShow, setRejectShow] = useState(false);
  const [index, setIndex] = useState("");
  const [selectSizes, setSelectSizes] = useState([]);
  const [products, setProducts] = useState([]);

  const handleClose = () => {
    setSuccessShow(false);
    setRejectShow(false);
  };

  useEffect(() => {
    getWishlist();
  }, []);

  const getWishlist = () => {
    DataService.getWishlist().then((data) => {
      //console.log(data)
      setWishlist(data.data.message);
      setLoading(false);
    });
  };

  const add_to_cart = (product, i) => {
    setIndex(i);
    setProducts(product);
    DataService.getProductDetail(product.item_code.split("-")[0]).then(
      (data) => {
        //console.log(data)
        setSelectSizes(data.data.message.sizes);
      }
    );
  };

  const selectedSize = (value) => {
    if (value !== "") {
      setCartLoading(true);
      //console.log(products,value)
      DataService.moveToCart(products, value).then((data) => {
        //console.log(data)
        if (data.data.message.Wishlist) {
          if (data.data.message.Wishlist.success_key === 1) {
            DataService.removeFromWishlist(products.item_code).then((data) => {
              if (data.data.message.success === 1) {
                setSuccessShow(true);
                setCartLoading(false);
                getWishlist();
              }
            });
          }
        }
        if (data.data.message.success_key === 1) {
          DataService.removeFromWishlist(products.item_code).then((data) => {
            if (data.data.message.success === 1) {
              setSuccessShow(true);
              setCartLoading(false);
              getWishlist();
            }
          });
        }
        if (data.data.message.success_key === 0) {
          DataService.removeFromWishlist(products.item_code).then((data) => {
            if (data.data.message.success === 1) {
              setRejectShow(true);
              setCartLoading(false);
              getWishlist();
            }
          });
        }
      });
    } else {
      setCartLoading(false);
    }
  };

  return (
    <Fragment>
      <div className="container no-padding black-bg wishlist">
        <div className="col-lg-12 ">
          <h3> My Wishlist</h3>
        </div>
      </div>
      <Snackbar
        open={successShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="success"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "4px",
              opacity: "1",
            },
          }}
          style={{
            background: "green",
            color: "white",
          }}
        >
          <b>Successfully Moved to Bag</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={rejectShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="info"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "3px",
              opacity: "1",
            },
          }}
          style={{
            background: "#5b91e1",
            color: "white",
            fontWeight: "500",
          }}
        >
          <b>Already present in Bag</b>
        </Alert>
      </Snackbar>
      {loading && (
        <div
          className="container-fluid text-center no-padding"
          style={{ padding: "10%" }}
        >
          <div className="col-lg-6 m-auto">
            <span className="spinner-border spinner-border-sm"></span>
          </div>
        </div>
      )}
      <section className="u-line section-padding">
        <div className="container">
          <div className="row">
            {wishlist.length > 0
              ? wishlist.map((item, i) => (
                  <>
                    <div key={i} className="col-12 col-md-4 mt-3">
                      <div className=" p-5 prod-box">
                        <Link
                          to={
                            "/detail/" +
                            item.item_code.split("-")[0] +
                            "#" +
                            item.item_code.split("-")[1]
                          }
                          className="text-decoration-none"
                        >
                          <RenderSmoothImage
                            src={item.image}
                            alt={item.item_code}
                            className="img-fluid"
                          />
                        </Link>
                        <h2 className="h5 text-center mt-3 mb-3">
                          {item.item_code}
                        </h2>
                        {item.discount_amount === 0 ? (
                          <h4
                            className="mt-3 d-flex align-items-center f-16"
                            style={{ justifyContent: "center" }}
                          >
                            Rs. {item.discounted_price}
                          </h4>
                        ) : (
                          <h4
                            className="mt-3 d-flex align-items-center f-16"
                            style={{ justifyContent: "center" }}
                          >
                            Rs. {item.discounted_price}
                            <span className="oldPrice line-through f-14">
                              Rs. {item.price}
                            </span>
                            <span className="discountPrice f-16">
                              (Rs. {item.discount_amount} OFF)
                            </span>
                          </h4>
                        )}

                        {cartLoading ? (
                          index === i ? (
                            <button className="btn btn-secondary btn-move-to-bag">
                              {cartLoading && (
                                <span className="spinner-border spinner-border-sm"></span>
                              )}{" "}
                              MOVING...
                            </button>
                          ) : (
                            <button className="btn btn-primary btn-move-to-bag">
                              MOVE TO BAG
                            </button>
                          )
                        ) : (
                          <button
                            className="btn btn-primary btn-move-to-bag"
                            onClick={() => add_to_cart(item, i)}
                            data-bs-toggle="modal"
                            data-bs-target="#selectSize"
                          >
                            MOVE TO BAG
                          </button>
                        )}
                      </div>
                    </div>
                  </>
                ))
              : !loading && (
                  <div
                    className="container-fluid text-center no-padding"
                    style={{ padding: "100px" }}
                  >
                    <div className="col-lg-6 m-auto">
                      <p>YOUR WISHLIST IS EMPTY</p>
                      <Link to="/" className="btn btn-primary me-4 grow4">
                        CONTINUE SHOPPING
                      </Link>
                    </div>
                  </div>
                )}
          </div>
        </div>
      </section>
      <br />
      <SelectSizeModal data={selectSizes} size={selectedSize} />
    </Fragment>
  );
};
