import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import DataService from "../../services/data.service";
import AddressModal from "./addressModal";

export const ChangeAddressModal = (props) => {
  let flag;
  const [address, setAddress] = useState([]);

  const handleRadioChange = (data, index) => {
    props.data(data, index);
  };

  useEffect(() => {
    getAddress();
    // eslint-disable-next-line
  }, []);

  const getAddress = () => {
    DataService.getAllAddress().then((data) => {
      //console.log(data)
      setAddress(data.data.message);
      flag = 1;
    });
  };
  if (flag === 1) {
    props.data(address.length > 0 ? address[0] : "", 0);
    flag = 0;
  }

  return (
    <>
      <div
        className="modal fade"
        id="changeAddress"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content p-4">
            <div className="modal-header border-0">
              <h4 className="modal-title" id="exampleModalLabel">
                Change address
              </h4>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            {address.length > 0 ? (
              address.map((item, i) => (
                <>
                  <div className="card mb-3">
                    <div className="card-body d-flex flex-wrap">
                      {i === 0 ? (
                        <input
                          className="form-check-input radio-position cus-radio"
                          name="address"
                          type="radio"
                          data-bs-toggle="modal"
                          data-bs-target="#changeAddress"
                          onClick={() => handleRadioChange(item, i)}
                          key={i}
                        />
                      ) : (
                        <input
                          className="form-check-input radio-position cus-radio"
                          name="address"
                          type="radio"
                          data-bs-toggle="modal"
                          data-bs-target="#changeAddress"
                          onClick={() => handleRadioChange(item, i)}
                          key={i}
                        />
                      )}
                      <div className="w-100 ms-5">
                        <h5 className="f-17">
                          <span className="tag-text ms-2 text-uppercase">
                            Home
                          </span>
                        </h5>
                        <p className="f-15 w-100 mt-4">
                          {item.house_no},{item.house_no}, {item.area},{" "}
                          {item.city}
                          <br /> {item.state} - {item.pincode}
                          <br />
                        </p>
                      </div>
                    </div>
                  </div>
                </>
              ))
            ) : (
              <div
                className="container-fluid text-center no-padding"
                style={{ padding: "100px" }}
              >
                <div className="col-lg-6 m-auto">
                  <p>Address not added yet</p>
                </div>
              </div>
            )}
            <div className="card mb-3">
              <div className="card-body px-xs-0 d-flex justify-content-between align-items-center">
                <h6 className="mb-0">Add New Address</h6>
                <button
                  className="btn btn-secondary"
                  data-bs-toggle="modal"
                  data-bs-target="#addAddress"
                >
                  ADD ADDRESS
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <AddressModal />
    </>
  );
};
