import React from "react";
import { Link } from "react-router-dom";
import RenderSmoothImage from "./RenderSmoothImage";
import "../core/styles.css";
//import product from '../data/product.json';

function SimilarProducts(props) {
  return (
    <section className="u-line section-padding">
      <div className="container">
        <div className="row">
          {
            props?.details?.length > 0 ?
              props.details.map((item, i) => (
                <div
                  key={i}
                  className="col-12 col-xxl-2 col-xl-3 col-lg-4 col-md-6 mt-3"
                >
                  <div className=" p-5 prod-box">
                    <Link to={"/detail/" + item.id + '#' + item.description.split('-')[1]}
                      className="text-decoration-none"
                    >
                      <RenderSmoothImage
                        src={item.image}
                        alt={item.title}
                        className="img-fluid"
                      />
                    </Link>
                    <h2 className="h5 text-center mt-3 mb-3">{item.title}</h2>
                    <p className="text-center">
                      {item.desc} <br></br>
                      <span className="price">{item.price}</span>
                    </p>
                  </div>
                </div>
              )) : ""
          }
        </div>
      </div>
    </section>
  );
}

export default SimilarProducts;
