import React from "react";
import cookie from "js-cookie";
import { useNavigate } from "react-router-dom";

const Menu = () => {
  const auth = cookie.get("user");
  const navigate = useNavigate();

  const logout = () => {
    if (window.confirm("You will be logged out of application")) {
      cookie.remove("user");
      navigate("/Login");
    }
  };

  return (
    <div className="app">
      <ul className="nav navbar-nav d-flex justify-content-between mx-lg-auto">
        <li className="nav-item">
          <a href="/bestseller" className="text-light-white fw-500">
            <span className="menu-items-name">Bestseller</span>
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-a" href="/category/Men">
            <span className="menu-items-name">Men</span>
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-a" href="/category/Women">
            <span className="menu-items-name">Women</span>
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-a" href="/category/Kids">
            <span className="menu-items-name">Kids</span>
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-a" href="/mybag">
            <span className="menu-items-name">My Cart</span>
          </a>
        </li>
        {auth ? (
          <li className="nav-item">
            <a className="nav-a" href="/myaccount">
              <span className="menu-items-name">My Account</span>
            </a>
          </li>
        ) : (
          []
        )}
        {auth ? (
          <li className="nav-item">
            <a className="nav-a" href=" " onClick={logout}>
              <span className="menu-items-name">Logout</span>
            </a>
          </li>
        ) : (
          <li className="nav-item">
            <a className="nav-a" href="/Login">
              <span className="menu-items-name">Login</span>
            </a>
          </li>
        )}
      </ul>
    </div>
  );
};

export default Menu;
