import React, { Fragment } from "react";
import { Link } from "react-router-dom";
//import DataService from "../../services/data.service";

const Pricedetail = (props) => {
  let cart = [];
  let price = 0;
  let discountedPrice = 0;
  let discountAmount = 0;
  //let coupon_discount = 0;
  let convenienceFee = 0;

  if (props.cart) {
    props.cart.forEach((item, i) => {
      if (item.out_of_stock === 0) {
        price = price + item.price;
        discountedPrice = discountedPrice + item.discounted_price;
        discountAmount = discountAmount + item.discount_amount;
        cart.push(item);
      }
    })
    if (discountedPrice < 599) {
      convenienceFee = 50;
    }
  }

  return (
    <Fragment>

      {/*<div className="card border-start-0 border-top-0 border-end-0 rounded-0 pb-4">
        <div className="card-header border-0 p-0 no-bg"><h5>Coupons</h5></div>
        <div className="card-body d-flex justify-content-between align-items-center">
          <span className="d-flex align-items-center"><i className="mr-2"><svg width="20" viewBox="0 0 23 23" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M1 1H10.7929C10.9255 1 11.0527 1.05268 11.1464 1.14645L21.1464 11.1464C21.3417 11.3417 21.3417 11.6583 21.1464 11.8536L11.8623 21.1377C11.6636 21.3364 11.3404 21.3324 11.1467 21.129L1.62752 11.1339C1.54523 11.0475 1.49655 10.9345 1.49028 10.8153L1 1.5" stroke="black" stroke-width="1.5" stroke-linecap="round" />
            <circle cx="7" cy="6" r="1.5" fill="#D0EDFF" />
          </svg></i><input type="text" className="pincode couponcode ms-2" placeholder="Enter coupon code" /></span>
          <button className="btn btn-secondary">APPLY</button>
        </div>
      </div>*/}
      <div className="card border-0">
        <div className="card-body py-4 my-1">
          <p className="m-0 f-16 f-500 customCheck text-black">Price Details ({props.cart.length} items)</p>
          <div className="row">
            <div className="col-12 d-flex justify-content-between f-18 mb-2 mt-3">
              <span>Total MRP</span><span className="f-500 ">Rs {price}</span>
            </div>
            <div className="col-12 d-flex justify-content-between mb-2 f-18">
              <span>Discount on MRP</span><span className="f-500 text-green">-Rs {discountAmount}</span>
            </div>
            {/*<div className="col-12 d-flex justify-content-between mb-2 f-18">
              <span>Coupon Discount</span>
              <span className="f-500 text-green">
                {
                  coupon_discount === 0 ?
                    <span className="priceDetail-base-action">Apply coupon</span>
                    :
                    "Rs " + coupon_discount
                }
              </span>
            </div>*/}
            <div className="col-12 d-flex mb-2 f-18">
              <span>Convenience Fee</span>
              <span className="f-500 ms-auto">Rs</span>
              <span className="line-through f-500">99</span>
              <span className="f-500 text-green ms-1">{convenienceFee === 0 ? "Free" : "Rs " + convenienceFee}</span>
            </div>
            <div className="col-12 d-flex justify-content-between align-items-center py-4 my-1 border-top">
              <p className="m-0 f-18 f-500 customCheck text-black">Total Amount</p>
              <span className="itemsAction f-18 f-500">Rs {discountedPrice + convenienceFee}</span>
            </div>
            <div className="col-12">
              {
                cart.length > 0 ?
                  <Link to="/address" state={{ from: cart, index: props.index }}>
                    <button className="btn btn-primary w-100" >PLACE ORDER</button>
                  </Link>
                  :
                  <button className="btn btn-primary w-100">PLACE ORDER</button>
              }
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default Pricedetail;
