import React, { forwardRef } from "react";
import { Link } from "react-router-dom";
import Menu from "./Menu";
import RenderSmoothImage from "./RenderSmoothImage";
import withClickOutside from "./withClickOutside";
const Navbar = forwardRef(({ open, setOpen }, ref) => {
  console.log(open);
  return (
    <div className="app">
      <div className="col-md-12 align-self-center navbar-collapse justify-content-lg-between topbar">
        <RenderSmoothImage
          src="/assets/images/arrow-left.png"
          className="card-img-top"
          alt="..."
        />
        <span> FREE HOME DELIVERY </span>
        <RenderSmoothImage
          src="/assets/images/arrow-right.png"
          className="card-img-top"
          alt="..."
        />
      </div>
      <nav className="navbar navbar-expand-lg navbar-light shadow" ref={ref}>
        <button
          className="navbar-toggler border-0"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#templatemo_main_nav"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
          onClick={() => setOpen(!open)}
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="container d-flex justify-content-between align-items-center">
          <div
            className="col-md-4 align-self-center collapse navbar-collapse flex-fill  d-lg-flex justify-content-lg-between"
            id="templatemo_search"
          >
            <input type="text" defaultValue="Search" className="searchBox" />
          </div>
          <div
            className="col-md-4 align-self-center collapse navbar-collapse flex-fill  d-lg-flex justify-content-lg-between"
            id="templatemo_logo"
          >
            <Link
              className="navbar-brand text-light logo h1 align-self-center"
              to="/"
            >
              <RenderSmoothImage
                src="/assets/images/logo.png"
                className="card-img-top"
                alt="..."
              />
            </Link>
          </div>
          {open && (
            <div
              className="col-md-4 align-self-center collapse navbar-collapse flex-fill  d-lg-flex justify-content-lg-between"
              id="templatemo_main_nav"
            >
              <div className="flex-fill">
                <Menu />
              </div>
            </div>
          )}
        </div>
      </nav>
      <div
        className="modal fade bg-white"
        id="templatemo_search"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-lg" role="document">
          <div className="w-100 pt-1 mb-5 text-right">
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <form
            action=""
            method="get"
            className="modal-content modal-body border-0 p-0"
          >
            <div className="input-group mb-2">
              <input
                type="text"
                className="form-control"
                id="inputModalSearch"
                name="q"
                placeholder="Search ..."
              />
              <button
                type="submit"
                className="input-group-text bg-success text-light"
              >
                <i className="fa fa-fw fa-search text-white"></i>
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
});
export default withClickOutside(Navbar);
