import React from "react";
import { Link, useNavigate } from "react-router-dom";
import "../core/styles.css";
import DataService from "../services/data.service";
import cookie from "js-cookie";
import Snackbar from "@mui/material/Snackbar";
import Alert from "@mui/material/Alert";
import { useState } from "react";

const ProductDetails = (props) => {
  const auth = cookie.get("user");

  const [inCartFlag, setInCartFlag] = useState(0);
  const [inWishlistFlag, setInWishlistFlag] = useState(0);

  const [successShow, setSuccessShow] = useState(false);
  const [rejectShow, setRejectShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [cartLoad, setCartLoad] = useState(false);
  const [successCartShow, setSuccessCartShow] = useState(false);
  const [rejectCartShow, setRejectCartShow] = useState(false);

  const handleClose = () => {
    setSuccessShow(false);
    setRejectShow(false);
    setSuccessCartShow(false);
    setRejectCartShow(false);
  };

  let sizeId = window.location.href.split("#")[1];
  let discounted_price;
  let original_price;
  let discount;

  const navigate = useNavigate();

  if (props.details.sizes) {
    props.details.sizes.forEach((item) => {
      if (item.id === sizeId) {
        discounted_price = item.discounted_price
          ? item.discounted_price
          : item.original_price;
        original_price = item.original_price;
        discount = item.discount ? item.discount : 0;
      }
    });
  }

  const add_to_wishlist = () => {
    if (auth) {
      setLoading(true);
      DataService.addToWishlist(
        props.details.item_code,
        sizeId,
        discounted_price,
        original_price,
        discount
      ).then((data) => {
        if (data.data.message.Wishlist) {
          if (data.data.message.Wishlist.success_key === 1) {
            setSuccessShow(true);
            setLoading(false);
            setInWishlistFlag(1);
          }
        }
        if (data.data.message.success_key === 1) {
          setSuccessShow(true);
          setLoading(false);
          setInWishlistFlag(1);
        }
        if (data.data.message.success_key === 0) {
          setRejectShow(true);
          setLoading(false);
          setInWishlistFlag(1);
        }
      });
    } else {
      navigate("/login");
    }
  };

  const add_to_cart = () => {
    setCartLoad(true);
    if (auth) {
      DataService.addToCart(
        props.details.item_code,
        sizeId,
        discounted_price,
        original_price,
        discount
      ).then((data) => {
        if (data.data.message.Wishlist) {
          if (data.data.message.Wishlist.success_key === 1) {
            setSuccessCartShow(true);
            setCartLoad(false);
            setInCartFlag(1);
          }
        }
        if (data.data.message.success_key === 0) {
          setRejectCartShow(true);
          setCartLoad(false);
          setInCartFlag(1);
        }
      });
    } else {
      navigate("/login");
    }
  };

  return (
    <>
      <Snackbar
        open={successShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="success"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "4px",
              opacity: "1",
            },
          }}
          style={{
            background: "green",
            color: "white",
          }}
        >
          <b>Successfully Added to Wishlist</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={rejectShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="info"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "3px",
              opacity: "1",
            },
          }}
          style={{
            background: "black",
            color: "white",
            fontWeight: "500",
          }}
        >
          <b>Already present in Wishlist</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={successCartShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="success"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "4px",
              opacity: "1",
            },
          }}
          style={{
            background: "green",
            color: "white",
          }}
        >
          <b>Successfully Added to Bag</b>
        </Alert>
      </Snackbar>
      <Snackbar
        open={rejectCartShow}
        autoHideDuration={1000000}
        onClose={handleClose}
        sx={{ height: "5%" }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Alert
          onClose={handleClose}
          severity="info"
          sx={{
            width: "100%",
            "& .MuiAlert-icon": {
              color: "white",
              marginTop: "3px",
              opacity: "1",
            },
          }}
          style={{
            background: "black",
            color: "white",
            fontWeight: "500",
          }}
        >
          <b>Already present in Bag</b>
        </Alert>
      </Snackbar>
      <section className="u-line section-padding">
        <div className="container">
          <div className="row">
            <div className="col-12 mt-3">
              <h2 className="f-600">{props.details.brand}</h2>
              <p>{props.details.item_name}</p>
              {props?.details?.sizes?.length > 0 ? (
                <h4 className="mt-30 d-flex align-items-center">
                  {sizeId === undefined
                    ? "Select size to view product price"
                    : props.details.sizes.map((text, i) =>
                        text.id === sizeId ? (
                          <span key={i}>
                            Rs.
                            {text.discounted_price
                              ? text.discounted_price
                              : text.discount
                              ? Math.round(
                                  (text.original_price -
                                    text.original_price *
                                      (text.discount / 100)) *
                                    10
                                ) / 10
                              : text.original_price}
                          </span>
                        ) : (
                          ""
                        )
                      )}
                  <span className="oldPrice line-through">
                    {props.details.sizes.map((text, i) =>
                      text.id === sizeId ? (
                        <span key={i}>
                          {text.discount ? "Rs." + text.original_price : ""}
                        </span>
                      ) : (
                        ""
                      )
                    )}
                  </span>
                  <span className="discountPrice">
                    {props.details.sizes.map((text, i) =>
                      text.id === sizeId ? (
                        <span key={i}>
                          {text.discount ? "(" + text.discount + "% OFF)" : ""}
                        </span>
                      ) : (
                        ""
                      )
                    )}
                  </span>
                </h4>
              ) : (
                ""
              )}
              <p>(Include of all taxes)</p>
              <h4 className="mt-30">Select Size</h4>
              {props?.details?.sizes?.length > 0 ? (
                <ul className="productSize">
                  {props.details.sizes.map((text, i) => {
                    return text.id === sizeId ? (
                      <li key={i}>
                        <Link
                          style={{
                            background: "#000",
                            color: "#fff",
                          }}
                          to={`#${text.id}`}
                        >
                          <span key={i}>{text.id}</span>
                        </Link>
                      </li>
                    ) : (
                      <li key={i}>
                        <Link to={`#${text.id}`}>
                          <span key={i}>{text.id}</span>
                        </Link>
                      </li>
                    );
                  })}
                </ul>
              ) : (
                ""
              )}
              <div className="d-flex justify-content-center align-items-center mt-5">
                {auth ? (
                  inCartFlag === 0 ? (
                    <Link
                      to={`#${sizeId}`}
                      className="btn btn-primary me-4 grow5"
                      onClick={add_to_cart}
                    >
                      {cartLoad && (
                        <span className="spinner-border spinner-border-sm"></span>
                      )}{" "}
                      ADD TO BAG
                    </Link>
                  ) : (
                    <Link to="/mybag" className="btn btn-primary me-4 grow5">
                      GO TO BAG
                    </Link>
                  )
                ) : (
                  <Link to="/Login" className="btn btn-primary me-4 grow5">
                    GO TO BAG
                  </Link>
                )}
                {inWishlistFlag === 0 ? (
                  <button
                    className="btn btn-outline-primary grow3 btn-wishlist"
                    onClick={add_to_wishlist}
                  >
                    {loading && (
                      <span className="spinner-border spinner-border-sm"></span>
                    )}{" "}
                    <span>ADD TO WISHLIST</span>
                  </button>
                ) : (
                  <button
                    disabled
                    className="btn btn-outline-primary grow3 btn-wishlist"
                  >
                    <span>WISHLISTED</span>
                  </button>
                )}
              </div>
              <h5 className="mt-5">Specifications</h5>
              <ul className="listStyle mt-3">
                <li>100% Original Products</li>
                <li>Pay on delivery might be available</li>
                <li>Easy 30 days returns &amp; exchanges</li>
                <li>Try &amp; Buy might be available</li>
              </ul>

              <h5 className="mt-5">Description</h5>
              <p>{props.details.description}</p>

              <h5 className="mt-5">Benefits</h5>
              <ul className="listStyle">
                <li>
                  The foam midsole feels comfortable for all-day wear and
                  combines with a soft sockliner that elevates the feel.
                </li>
                <li>
                  The suede upper adds a retro, athletics-inspired look while
                  airy mesh keeps it breathable.
                </li>
                <li>
                  The reflective design dots, zig-zag stitching and "NIKE
                  SPORTSWEAR" on the heel clip add premium details with a modern
                  edge.
                </li>
                <li>
                  The rubber Waffle-inspired sole adds Nike DNA, excellent
                  traction and durability.
                </li>
                <li>
                  The thin TPU heel clip adds a twist to OG Nike running while
                  adding stability.
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default ProductDetails;
