import React, { Fragment, useEffect, useState } from "react";
import BestSeller from "../../core/BestSeller";
import DataService from "../../services/data.service";

const Content = () => {
  const [bestselling, setBestselling] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    DataService.getProduct()
      .then((data) => {
        setBestselling(data.data.message.best_selling_products)
        setLoading(false)
      })
  }, []);

  return (
    <Fragment>
      <div className="container-fluid text-center no-padding black-bg">
        <div className="col-lg-6 m-auto">
          <h3 className="h3">best seller</h3>
        </div>
      </div>
      {loading && (
        <div className="container-fluid text-center no-padding" style={{ padding: '10%' }}>
          <div className="col-lg-6 m-auto">
            <span className="spinner-border spinner-border-sm"></span>
          </div>
        </div>
      )}
      <section className="container py-5">
        <div className="row">
          <BestSeller bestselling={bestselling} />
        </div>
      </section>
    </Fragment>
  );

}

export default Content;
